@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-family:'Poppins', sans-serif;font-weight:700}h1,h2,h3,h4,h5,h6{color:#264653}

.page-home{width:100vw;height:100vh}.home-top{width:100%;display:flex;flex-direction:row;align-items:center;justify-content:space-around;text-align:center;margin-top:70px}.home-activity{display:flex;flex-direction:column;align-items:center;justify-content:center;text-align:center;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;font-size:30px}.home-timer{display:flex;flex-direction:column;align-items:center;justify-content:center;text-align:center;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;font-size:50px}button{background-color:#2A9D8F;color:white;border:none;padding:5px 20px;font-size:20px}.home-bottom-bar{width:100vw;height:25vh;background-color:#264653;position:fixed;bottom:0}.home-bottom{display:flex;flex-direction:row;align-items:center;justify-content:space-around;text-align:center;position:absolute;width:100%;bottom:30%}

